import React from "react";
import { animated, useSpring } from "react-spring";
import { useDrag } from "@use-gesture/react";
import styled from "styled-components";
import { colors } from "../../styles/theme";

//    Props
interface MobileSlidingPanelProps {
  content?: React.ReactNode;
}

//      
const MobileSlidingPanel: React.FC<MobileSlidingPanelProps> = ({ content }) => {
  //    ()
  const NAVBAR_HEIGHT = 100;
  //     (window   500 fallback)
  const panelHeight =
    typeof window !== "undefined" ? window.innerHeight - NAVBAR_HEIGHT : 500;

  //  3  
  const MID_POSITION = panelHeight - 300; //  
  const FULLY_OPEN = 0; //    ()
  const FULLY_CLOSED = panelHeight; //    ()

  // react-spring y    
  const [{ y }, api] = useSpring(() => ({
    y: FULLY_CLOSED,
    config: { tension: 100, friction: 25 },
  }));

  //  /    (   )
  const togglePanel = () => {
    api.start({ y: y.get() === FULLY_CLOSED ? MID_POSITION : FULLY_CLOSED });
  };

  //   
  const bind = useDrag(
    ({ down, movement: [, my] }) => {
      if (down) {
        //  : y    ( )
        api.start({
          y: Math.max(FULLY_OPEN, Math.min(FULLY_CLOSED, y.get() + my)),
        });
      } else {
        //  :     
        const currentY = y.get();
        const target = getNearestSnapPoint(currentY);
        api.start({ y: target });
      }
    },
    { from: () => [0, y.get()] }
  );

  //  y      
  const getNearestSnapPoint = (currentY: number) => {
    const points = [FULLY_OPEN, MID_POSITION, FULLY_CLOSED];
    return points.reduce((prev, curr) =>
      Math.abs(curr - currentY) < Math.abs(prev - currentY) ? curr : prev
    );
  };

  //    
  const AnimatedPanel = animated(PanelBase);

  return (
    <MobileContainer>
      <AnimatedPanel
        style={{ transform: y.to((v) => `translateY(${v}px)`) }}
        {...bind()}
      >
        <SwipeBar onClick={togglePanel} />
        <MobileContentWrapper>
          {/*    max-width scale  */}
          <MobileContent>
            <CardScaleWrapper>{content}</CardScaleWrapper>
          </MobileContent>
        </MobileContentWrapper>
      </AnimatedPanel>
    </MobileContainer>
  );
};

// ===== styled-components =====

//    (  )
const MobileContainer = styled.div`
  position: fixed;
  bottom: 0;
  width: 100%;
  z-index: 20;
`;

//   ( )
const PanelBase = styled.div`
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 100vh;
  background-color: ${colors.secondary};
  border-top-left-radius: 20px;
  border-top-right-radius: 20px;
  touch-action: none;
  display: flex;
  flex-direction: column;
`;

//   (   )
const SwipeBar = styled.div`
  width: 200px;
  height: 5px;
  background-color: #ccc;
  border-radius: 5px;
  margin: 10px auto 0 auto;
  cursor: pointer;
  flex-shrink: 0;
`;

//   ( )
const MobileContentWrapper = styled.div`
  flex: 1 1 0;
  min-height: 0;
  display: flex;
  flex-direction: column;
  overflow: hidden;
`;

//    ( )
const MobileContent = styled.div`
  flex: 1 1 0;
  min-height: 0;
  padding: 12px 0 100px 0;
  color: white;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  scrollbar-width: none; /* Firefox */
  -ms-overflow-style: none; /* IE 10+ */
  display: flex;
  flex-direction: column;
  align-items: center;
  &::-webkit-scrollbar {
    display: none; /* Chrome, Safari, Opera */
  }
`;

//   scale max-width 
const CardScaleWrapper = styled.div`
  width: 100%;
  max-width: 440px;
  transform: scale(0.9);
  transform-origin: top center;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  gap: 16px;
`;

export default MobileSlidingPanel;
