import styled from "styled-components";
import NaverMap from "../components/NaverMap";
import SlidingPanel from "../components/SlidingPanel";
import Card from "../components/Card";
import { useEffect, useState } from "react";
import { baseURL } from "../api/api";
import axios from "axios";
import {
  ApiSpaceMarkerResponse,
  ApiSpaceResponse,
  SpaceWithMarker,
} from "@/types/responses";

/**
 * SpaceMap 
 * -  SlidingPanel(  ),  NaverMap() 
 * - SlidingPanel content prop    
 * - CardList        
 */
const SpaceMap: React.FC = () => {
  const [spaceList, setSpaceList] = useState<SpaceWithMarker[]>([]); //   

  useEffect(() => {
    const fetch = async () => {
      try {
        const [eduListRes, markerRes] = await Promise.all([
          axios.get<ApiSpaceResponse[]>(`${baseURL}/api/space`),
          axios.get<ApiSpaceMarkerResponse[]>(`${baseURL}/api/space/marker`),
        ]);

        const merged: SpaceWithMarker[] = eduListRes.data
          .map((space) => {
            const marker = markerRes.data.find(
              (m) => m.spaceId === space.spaceId
            );

            if (!marker) return null;

            return {
              ...space,
              lat: marker.spaceLocationLatitude!,
              lng: marker.spaceLocationLongitude!,
            };
          })
          .filter(Boolean) as SpaceWithMarker[];

        setSpaceList(merged);
      } catch (err) {
        console.error(" API  ", err);
      }
    };

    fetch();
  }, []);

  return (
    <SpaceMapContainer>
      {/*  :    */}
      <SlidingPanel
        content={
          <CardListWrapper>
            <CardList>
              {spaceList.map((space, id) => (
                <Card key={id} type="space" data={space} />
              ))}
            </CardList>
          </CardListWrapper>
        }
      />
      {/*    */}
      <NaverMap
        spaceMarkers={spaceList.map((m) => ({
          spaceId: m.spaceId!,
          lat: m.lat!,
          lng: m.lng!,
        }))}
      />
    </SpaceMapContainer>
  );
};

/**
 * CardListWrapper
 * - SlidingPanel  CardList      
 */
const CardListWrapper = styled.div`
  height: 100%;
  min-height: 0;
  display: flex;
  flex-direction: column;
`;

/**
 * CardList
 * -   ,  
 * -   
 */
const CardList = styled.div`
  display: flex;
  flex-direction: column;
  gap: 20px;
  padding: 24px 8px 24px 8px;
  overflow-y: auto;
  height: 100%;
  min-height: 0;
  scrollbar-width: none; /* Firefox */
  -ms-overflow-style: none; /* IE 10+ */
  &::-webkit-scrollbar {
    display: none; /* Chrome, Safari, Opera */
  }
`;

/**
 * SpaceMapContainer
 * -   , 100vw/100vh 
 */
const SpaceMapContainer = styled.div`
  width: 100%;
  height: 100vh;
  position: relative;
`;

export default SpaceMap;
